.button {
  cursor: pointer;
  -o-transition: all .2s ease;
  justify-content: center;
  align-items: center;
  transition: all .2s;
  display: flex;
}

.button:disabled, .button[disabled] {
  opacity: .7;
  cursor: not-allowed;
  pointer-events: none;
}

.button:active {
  transform: scale(.95);
}

.button.primary {
  background-color: var(--base-200);
  font-family: var(--font-family-first), Arial, sans-serif;
  color: var(--base-content-100);
  -webkit-backdrop-filter: blur(2px);
  text-transform: uppercase;
  border: none;
  border-radius: 20px;
  outline: 1px solid #fff3;
  padding: 24px;
  font-size: 16px;
  font-weight: 500;
  box-shadow: 0 4px 30px #0000001a;
}

.button.primary:hover, .button.primary:focus-visible {
  box-shadow: 0 0 10px var(--base-200);
  outline: 2px solid var(--base-200);
  outline-offset: 2px;
}

.button.primary span {
  position: relative;
  top: -2px;
}

@media (hover: none) {
  .button.primary:hover {
    box-shadow: 0 4px 30px #0000001a;
  }
}
